<script>
import Timelineitem from "@/components/timelineitem";

export default {
  name: "timeline",
  components: {Timelineitem},
  mounted() {


    (function ($) {
      $.fn.timeline = function () {
        var selectors = {
          id: $(this),
          item: $(this).find(".item"),
          activeClass: "item--active",
          img: ".img"
        };
        // 将第一个时间轴项目激活，并设置时间轴背景图片为第一个项目的图片
        console.log(selectors.item.first()
            .find(selectors.img)
            .attr("src"))
        selectors.item.eq(0).addClass(selectors.activeClass);
        selectors.id.css(
            "background-image",
            "url(" +
            "img/img.png" +
            ")"
        );
        // 获取时间轴项目的总数
        var itemLength = selectors.item.length;
        // 当页面滚动时，触发滚动事件
        $(window).scroll(function () {

          var max, min;
          // 获取页面滚动的距离
          var pos = $(this).scrollTop();
          // console.log("滚动距离：", pos)
          selectors.item.each(function (i) {
            // 获取当前时间轴项目的最小和最大高度
            min = $(this).offset().top;
            max = $(this).height() + $(this).offset().top;
            // console.log(min,max)
            var that = $(this);
            if (i == itemLength - 2 && pos >= min) {
              selectors.item.removeClass(selectors.activeClass);
              selectors.id.css(
                  "background-image",
                  "url(" +
                  selectors.item.last()
                      .find(selectors.img)
                      .attr("src") +
                  ")"
              );
              selectors.item.last().addClass(selectors.activeClass);
            }
                // 如果当前滚动位置在当前项目的最小和最大高度之间，
            // 则将当前项目设置为激活状态，并设置背景图片为当前项目的图片
            else if (pos <= max - 200 && pos >= min - 200) {
              if($(this).find(selectors.img).attr("src")!=="img/01.28079b8c.gif"){

              selectors.id.css(
                  "background-image",
                  "url(" +
                  $(this)
                      .find(selectors.img)
                      .attr("src") +
                  ")"
              );
              selectors.item.removeClass(selectors.activeClass);
              $(this).addClass(selectors.activeClass);}
              else {
                selectors.id.css(
                    "background-image",
                    "url(" +
                    "img/img.png" +
                    ")"
                );
                selectors.item.removeClass(selectors.activeClass);
                $(this).addClass(selectors.activeClass);

              }
            }
          });
        });
      };
    })(jQuery)
    /*
    最后，我们调用 timeline 插件并传入时间轴的 ID 作为参数。
    这将启用时间轴插件并为该时间轴绑定滚动事件。
    */
    $("#shell").timeline();
  },
  methods: {
    playmusic() {
      this.isplay = !this.isplay
      if (this.isplay) {
        this.$refs.bgm.play();
      } else {
        this.$refs.bgm.pause();
      }
    },
    back() {
      this.$router.go(-1)
    }
  },

  data() {
