          desc: '攀枝花卫生学校并入攀枝花大学并保留校名，成为集大中专教学于一体的攀枝花普通高等医学教育和成人高等医学教育相结合的新型学校。攀枝花卫生学校校为基础组建的攀枝花大学医学系成立。同年，被省教委授予“省级校风示范学校”称号。'
        }, {
          text: '攀枝花学院',
          img: '../img/06.jpg',
          title: '2001'
          , desc: '攀枝花大学升建成为普通本科学院，更名攀枝花学院。学院以本科教育为主，同时举办专科层次高等职业教育，实行省市共建、以省为主的办学体制。'
        }, {
          text: '攀枝花学院正式成立',
          img: '../img/07.png',
          title: '2002'
          , desc: '攀枝花学院举行挂牌庆典活动，攀枝花学院正式成立。'
        }, {
          text: '攀枝花学院获学士学位授予权',
