        }, {
          text: '学校入选“教育部·中兴通讯ICT产教融合创新基地项目”第二批合作高校',
          img: '../img/12.jpg',
          title: '2016'
          , desc: '同年，学校入选四川省职务科技成果权属混合所有制改革试点单位。'
        }, {
          text: '四川省知识产权培育单位',
          img: '../img/13.jpg',
          title: '2017'
          , desc: '攀枝花学院入选四川省知识产权优势培育单位。'
        }, {
          text: '攀枝花学院华为信息与网络技术学院',
          img: '../img/14.jpg',
