          , desc: '学校与华为技术有限公司签订校企合作协议，共建攀枝花学院华为信息与网络技术学院。'
        }, {
          text: '学校获批硕士学位授予单位',
          img: '../img/15.jpg',
          title: '2021'
          , desc: 'adasda'
        },]


