import Vue from 'vue'
import VueRouter from 'vue-router'
import timeline from "@/views/timeline";
import Dashboard from "@/views/dashboard";

Vue.use(VueRouter)

const routes = [
    {
        path: '/',
        name: '',
        component: Dashboard,
        redirect: '1',
        children: [
            {
                path: '1',
                component: () => import('../views/首页')
            },
            {
                path: '2',
                component: () => import('../views/校庆动态')
            },
            {
                path: '3',
                component: () => import('../views/timeline')
            },
            {
                path: '5',
                component: () => import('../views/校园风光')
            },

            {
                path: '6',
                component: () => import('../views/师生风采')
            },]

    }, {
        path: '/timeline',
        name: '',
        component: timeline,

    }

]

const router = new VueRouter({
    mode:"hash",
    routes
})

export default router
